
import { Carousel } from "antd";
import { useEffect,useState } from "react";
import axios from "axios";
const contentStyle = {
    width : "100%",
    color: "#fffff",
    height: "400px",
    textAlign: "center",
    background: "#44fe9f",
  };

  export default function Carouse(){

    const [images, setimages] = useState([])
    useEffect(() => {
      axios("/v1/")
      .then(response=>setimages(response.data.data.header_banners))
      
    }, [])
    console.log(images)
    
      return(        <div>

        <Carousel autoplay>
{images.map(img=>(          <div key={img.id}>
            <img style={contentStyle}  src={img.image} alt="images not loading"></img>
          
          </div>))}
        </Carousel>
      </div>
      );
  };
